<template>
  <div id="app">
    <audio
      :src="songSrc"
      autoplay
      ref="audio"
      @play="playing = true"
      @pause="playing = false"
      @durationchange="duration = $event.target.duration"
      @timeupdate="currentTime = $event.target.currentTime"
    ></audio>
    <PlayBar
      @getSong="getSong"
      :song="song"
      :playing="playing"
      @song-pause="$refs.audio.pause()"
      @song-play="$refs.audio.play()"
      v-show="!play_page_show && song != null"
      @openPlayMenu="openPlayMenu"
    ></PlayBar>
    <PlayMenu
      v-show="playmenushow"
      @closePlayMenu="closePlayMenu"
      :historyplaylist="historyplaylist"
      :nowplaylist="nowplaylist"
      @getSong="getSong"
      @removemusic="removemusic"
    ></PlayMenu>
    <PlayPage
      v-show="play_page_show"
      @playPageClose="playPageClose"
      :song="song"
      :playing="playing"
      @song-pause="$refs.audio.pause()"
      @song-play="$refs.audio.play()"
      :duration="duration"
      :currentTime="currentTime"
      @update-time="$refs.audio.currentTime = $event"
      :songLic="songLic"
      @moduleChange="moduleChange"
      :playmodle="playmodle"
      @openPlayMenu="openPlayMenu"
      @songbefore="songbefore"
      @songafter="songafter"
    ></PlayPage>
    <router-view
      :song="song"
      :playing="playing"
      @getSong="getSong"
      @song-pause="$refs.audio.pause()"
      @song-play="$refs.audio.play()"
      :hissearch="hissearch"
      @his="his"
      @getList="getList"
    />
  </div>
</template>

<script>
import PlayBar from "./components/PlayBar.vue";
import PlayPage from "./components/PlayPage.vue";
import PlayMenu from "./components/PlayMenu.vue";
export default {
  name: "app",
  data: function () {
    return {
      song: JSON.parse(localStorage.getItem("historyplay")) || null,
      playing: false,
      play_page_show: false,
      duration: 0,
      currentTime: Number(localStorage.getItem("currentTime")) || 0,
      songLic: JSON.parse(localStorage.getItem("songLic")) || null,
      playmodle: "playargin",
      playmenushow: false,
      historyplay: JSON.parse(localStorage.getItem("historyplay")) || [],
      historyplaylist:
        JSON.parse(localStorage.getItem("historyplaylist")) || [],
      hissearch: JSON.parse(localStorage.getItem("hissearch")) || [],
      nowplaylist: JSON.parse(localStorage.getItem("nowplaylist")) || [],
    };
  },
  computed: {
    songSrc: function () {
      return this.song
        ? `https://music.163.com/song/media/outer/url?id=${this.song?.id}.mp3`
        : null;
    },
    songindex: function () {
      return this.nowplaylist.findIndex((o) => o.id == this.song.id);
    },
  },
  methods: {
    random: function () {
      return Math.round(Math.random() * (this.nowplaylist.length - 1));
    },
    his: function (keyword) {
      this.hissearch = [...new Set([keyword, ...this.hissearch])];
    },
    getSong: function (e) {
      this.play_page_show = true;
      this.playmenushow = false;
      if (e != undefined) {
        this.song = e;
        this.historyplay = e;
        this.historyplaylist = [...new Set([e, ...this.historyplaylist])];
        this.nowplaylist = [...new Set([e, ...this.nowplaylist])];
        // this.axios
        // .get("https://apis.netstart.cn/music/song/detail", {
        //   params: { ids },
        // })
        // .then((res) => {
        //   this.x = res.data.songs;
        // });
        if (this.nowplaylist.map((o) => o.id).indexOf(this.song.id) == -1) {
          this.nowplaylist.push(this.song);
        } else {
          this.nowplaylist;
        }
      }
    },
    removemusic:function (e) {
      this.nowplaylist = [...this.nowplaylist.filter(o=>o.id != e.id)];
      localStorage.setItem("nowplaylist", JSON.stringify(this.nowplaylist));
    },
    getList: function (e) {
      // if (e.trackIds == e.tracks) {
        this.nowplaylist = e.tracks;
        this.song = e.tracks[0];
        this.historyplay = e.tracks[0];
        this.historyplaylist = [...new Set([e.tracks[0], ...this.historyplaylist])];
      // } else {
      //   this.axios
      //     .get("https://apis.netstart.cn/music/playlist/track/all", {
      //       params: { id: e.id,limit: 40 },
      //     })
      //     .then((res) => {
      //       this.nowplaylist = res.data.songs;
      //       this.song = res.data.songs[0];
      //       this.historyplay = res.data.songs[0];
      //       this.historyplaylist = [
      //         ...new Set([res.data.songs[0], ...this.historyplaylist]),
      //       ];
      //     });
      // }
    },
    songbefore: function () {
      if (this.playmodle == "playrandom") {
        this.getSong(this.nowplaylist[this.random()]);
      } else {
        if (this.songindex == 0) {
          this.getSong(this.nowplaylist[this.nowplaylist.length - 1]);
        } else {
          this.getSong(this.nowplaylist[this.songindex - 1]);
        }
      }
    },
    songafter: function () {
      if (this.playmodle == "playrandom") {
        this.getSong(this.nowplaylist[this.random()]);
      } else {
        if (this.songindex == this.nowplaylist.length - 1) {
          this.getSong(this.nowplaylist[0]);
        } else {
          this.getSong(this.nowplaylist[this.songindex + 1]);
        }
      }
    },
    getPic: function (ids) {
      this.axios
        .get("https://apis.netstart.cn/music/song/detail", {
          params: { ids },
        })
        .then((res) => {
          this.x = res.data.songs;
        });
    },
    playPageClose: function () {
      this.play_page_show = false;
    },
    getLyc: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/lyric", {
          params: { id },
        })
        .then((res) => {
          this.songLic = res.data;
          localStorage.setItem("songLic", JSON.stringify(res.data));
        });
    },
    moduleChange: function (module) {
      this.playmodle = module;
      // console.log(module);
    },
    openPlayMenu: function () {
      this.playmenushow = true;
    },
    closePlayMenu: function () {
      this.playmenushow = false;
    },
  },
  watch: {
    hissearch: function (newhissearch) {
      localStorage.setItem("hissearch", JSON.stringify(newhissearch));
    },
    song: function (newsong) {
      this.getLyc(newsong.id);
    },
    currentTime: function (newcurrentTime) {
      localStorage.setItem("currentTime", JSON.stringify(newcurrentTime));
      if (this.currentTime == this.duration && this.playmodle == "playargin") {
        this.currentTime = 0;
        this.$refs.audio.play();
      }
      if (this.currentTime == this.duration && this.playmodle == "playrandom") {
        this.getSong(this.nowplaylist[this.random()]);
        this.currentTime = 0;
        this.$refs.audio.play();
      }
    },
    historyplay: function (newhistoryplay) {
      localStorage.setItem("historyplay", JSON.stringify(newhistoryplay));
      localStorage.setItem(
        "historyplaylist",
        JSON.stringify(this.historyplaylist)
      );
      localStorage.setItem("nowplaylist", JSON.stringify(this.nowplaylist));
    },
  },
  mounted() {
    this.$refs.audio.currentTime = this.currentTime;
  },
  components: { PlayBar, PlayPage, PlayMenu },
};
</script>

<style lang="less" scoped>
audio {
  position: fixed;
  bottom: 50%;
  z-index: 100;
}
</style>
