<template>
  <div>
    <div class="back" @click="$router.back()">
      <img src="@/assets/back.svg" alt="" />
    </div>
    <div class="loading" v-if="loading_show">
      <img src="@/assets/loading.gif" alt="" />
    </div>
    <div v-else class="list">
      <ul>
        <SearchSong
          v-for="item in songlist"
          :item="item"
          :key="item?.id"
          @getSong="$emit('getSong', $event)"
          :playing="playing"
          :song="song"
        ></SearchSong>
      </ul>
    </div>
  </div>
</template>

<script>
import SearchSong from "../components/SearchSong.vue";

export default {
  props: {
    playing: Boolean,
    song: Object,
  },
  data: function () {
    return {
      songlist: [],
      loading_show: false,
    };
  },
  created() {
    this.$emit("his", this.$route.query.keywords);
    this.loading_show = true;
    this.axios
      .get("https://apis.netstart.cn/music/cloudsearch", {
        params: { keywords: this.$route.query.keywords },
      })
      .then((res) => {
        (this.loading_show = false), (this.songlist = res.data.result.songs);
      });
  },
  components: { SearchSong },
};
</script>

<style lang="less" scoped>
div {
  .loading {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .back {
    width: 100%;
    height: 30rem;
    text-align: center;
    background-color: #22d59c;
    position: fixed;
    z-index: 500;
    img {
      height: 30rem;
      width: 30rem;
    }
  }
  .list {
    padding: 30rem 0 50rem;
    overflow-x: hidden;
  }
}
</style>
