<template>
  <div>
    <div class="playLyric" ref="scr" v-if="songLic">
      <ul>
        <li
          v-for="(lrc, index) in songlrc"
          :key="lrc.time"
          :class="{ active: activelrc.index == index }"
          ref="alrc"
        >
          {{ lrc.text }}
        </li>
      </ul>
    </div>
    <div class="songLyrnull" v-else>歌词加载中</div>
  </div>
</template>

<script>
export default {
  props: {
    songLic: Object,
    currentTime: Number,
  },
  data: function () {
    return {
      alrcHeight: 0,
      moveCount: 0,
    };
  },
  computed: {
    songlrc: function () {
      let lyric = this.songLic?.lrc.lyric;
      let arr = lyric?.split("\n");
      let parsed = arr
        ?.filter((o) => o)
        .map((o) => {
          var { groups } = o?.match(
            /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/
          );
          return {
            time: groups?.mm * 60 + groups.ss * 1,
            text: groups?.text.trim(),
          };
        });
      let filtparsed = parsed?.filter((o) => o.text != "");
      return filtparsed;
    },
    activelrc: function () {
      if (this.songlrc) {
        var copy = [...this.songlrc].reverse();
        var index = copy.findIndex((o) => o.time < this.currentTime);
      }

      return {
        index: copy?.length - 1 - index,
        text: copy[index]?.text,
      };
    },
  },
  methods: {
    move: function (index) {
      if (index == 0) {
        return this.$refs.alrc[0]?.clientHeight;
      }
      return this.move(index - 1) + this.$refs.alrc[index]?.clientHeight + 40;
    },
  },
  watch: {
    currentTime: function () {
      // let height = this.$refs.alrc;
      // console.log(this.$refs.alrc[1].clientHeight);
      // this.alrcHeight = height;
      this.activelrc
        ? ((this.moveCount = this.move(this.activelrc.index)),
          isNaN(this.moveCount) ? (this.moveCount = 0) : this.moveCount)
        : null;
      // console.log(this.$refs.scr.scrollTop);
      this.$refs.scr.scrollTo(0, this.moveCount);
      // console.log(this.moveCount);
      // console.log(this.moveCount);
      // console.log(this.songlrc);
    },
  },
};
</script>

<style lang="less" scoped>
.playLyric {
  color: #e6e6e677;
  overflow-y: scroll;
  height: 400rem;
  margin: 0 20rem;
  text-align: center;
  font-size: 17rem;
  letter-spacing: 2rem;
  &::-webkit-scrollbar {
    display: none;
  }
  ul {
    transform: translateY(200rem);
    padding-bottom: 100rem;
    li {
      margin: 40rem 0;
    }
    .active {
      color: white;
      font-weight: bold;
    }
  }
}
.songLyrnull {
  color: white;
  height: 400rem;
  margin: 0 20rem;
  text-align: center;
  font-size: 17rem;
  letter-spacing: 2rem;
  transform: translateY(200rem);
}
</style>
