<template>
  <div>
    <div class="back" @click="$router.back()">
      <img src="@/assets/back.svg" alt="" />
      歌单
    </div>
    <div class="loading" v-if="loading_show">
      <img src="@/assets/loading.gif" alt="" />
    </div>
    <section v-else>
      <div class="playlist-msg">
        <div>
          <div class="playlist-pic">
            <img src="" alt="" />
            <img
              :src="
                play_list.coverImgUrl + '?imageView=1&type=webp&thumbnail=270x0'
              "
              alt=""
            />
          </div>
          <div class="playlist-title">
            <h2>{{ play_list.name }}</h2>
            <div class="playlist-auth">
              <p>
                <img :src="play_list.creator?.avatarUrl" alt="" />
                <span>{{ play_list.creator?.nickname }}</span>
              </p>
              <p>播放量：{{ play_count }}</p>
            </div>
          </div>
        </div>
        <div>
          <div>
            <span @click="$emit('getList',play_list)">
              <i>
                <img src="@/assets/play-fill.svg" alt="" />
              </i>
              <span
                >全部播放({{ play_list.trackIds.length }})</span
              >
            </span>
          </div>
        </div>
      </div>
      <div class="list">
        <ul>
          <li
            v-for="item in list"
            :key="item.id"
            @click="$emit('getSong', item)"
          >
            <div>
              <div>
                <h3>
                  <span
                    >{{ item.name
                    }}{{
                      item.alia.length == 0
                        ? ""
                        : "(" + item.alia.join("") + ")"
                    }}</span
                  >
                </h3>
                <p>
                  <span>
                    {{
                      item.ar.map((o) => o.name).join(" / ") +
                      " · " +
                      item.al.name
                    }}</span
                  >
                </p>
              </div>
            </div>
          </li>
        </ul>
        <div @click="getList(offset)">
          <span v-show="!moreloading && list.length < play_list.trackIds.length"
            >点击加载更多歌曲</span
          >
          <span
            v-show="!moreloading && list.length >= play_list.trackIds.length"
            >到底了！！！</span
          >
          <img v-show="moreloading" src="@/assets/loading.gif" alt="" />
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      moreloading: false,
      loading_show: false,
      play_list: [],
      list: [],
      offset: 0,
    };
  },
  computed: {
    play_count: function () {
      let num = this.play_list.playCount?.toString();
      if (num?.length <= 4) {
        return this.play_list.playCount;
      } else if (num?.length <= 8) {
        return (this.play_list.playCount / Math.pow(10, 4)).toFixed(1) + " 万";
      } else {
        return (this.play_list.playCount / Math.pow(10, 8)).toFixed(1) + " 亿";
      }
    },
  },
  methods: {
    getList: function (offset) {
      this.moreloading = true;
      this.axios
        .get("https://apis.netstart.cn/music/playlist/track/all", {
          params: { id: this.$route.query.id, limit: 20, offset },
        })
        .then((res) => {
          this.moreloading = false;
          this.list.push(...res.data.songs);
          this.offset += 20;
        });
    },
  },
  created() {
    // console.log(123);
    this.loading_show = true;
    Promise.all([
      this.axios.get("https://apis.netstart.cn/music/playlist/detail", {
        params: { id: this.$route.query.id },
      }),
      this.axios.get("https://apis.netstart.cn/music/playlist/track/all", {
        params: { id: this.$route.query.id, limit: 20, offset: 0 },
      }),
    ]).then(([d, a]) => {
      this.loading_show = false;
      this.play_list = d.data.playlist;
      this.list = a.data.songs;
      this.offset += 20;
    });
  },
};
</script>

<style lang="less" scoped>
.back {
  width: 100%;
  height: 30rem;
  background-color: #22d59c;
  position: fixed;
  z-index: 500;
  font-size: 20rem;
  text-align: center;
  color: white;
  font-weight: 500;
  img {
    position: absolute;
    left: 15rem;
    height: 30rem;
    width: 30rem;
  }
}
.loading {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.playlist-msg {
  position: relative;
  padding-top: 40rem;
  & > div:first-child {
    display: -webkit-box;
    -webkit-box-align: center;
    -webkit-box-orient: vertical;
    margin: 0 0 15rem;
    .playlist-pic {
      position: relative;
      display: block;
      margin-bottom: 25rem;
      width: 190rem;
      height: 190rem;
      border-radius: 18rem;
      box-shadow: 0 7rem 13rem rgb(0 0 0 / 21%);
      background-color: #dbdbdb;
      -webkit-user-select: none;
      & > img:first-child {
        position: absolute;
        top: 0;
        left: 0;
        height: 20rem;
        z-index: 4;
        border-top-left-radius: 18rem;
      }
      & > img:last-child {
        position: relative;
        z-index: 1;
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 18rem;
      }
    }
    .playlist-title {
      max-width: 84%;
      text-align: center;
      h2 {
        width: 100%;
        font-weight: 400;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        -webkit-user-select: none;
        margin-bottom: 12rem;
        font-size: 18rem;
      }
      .playlist-auth {
        width: 100%;
        font-weight: 400;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        -webkit-user-select: none;

        & > p:first-child {
          padding-left: 8rem;
          height: 23rem;
          display: -webkit-box;
          -webkit-box-pack: center;
          -webkit-box-align: center;
          width: 100%;
          font-weight: 400;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          -webkit-user-select: none;
          img {
            display: block;
            margin-left: -8rem;
            width: 23rem;
            height: 23rem;
            border-radius: 20rem;
          }
          span {
            margin-left: 5rem;
            font-size: 14rem;
            line-height: 24rem;
            display: -webkit-flex;
            max-width: 250rem;
          }
        }
        & > p:last-child {
          margin-top: 10rem;
          font-size: 14rem;
        }
      }
    }
  }
  & > div:last-child {
    height: 55rem;
    display: flex;
    div {
      position: relative;
      display: -webkit-box;
      -webkit-box-align: center;
      padding: 0 21rem;
      width: 100%;
      box-sizing: border-box;
      & > span:first-child {
        display: -webkit-box;
        -webkit-box-align: center;
        font-size: 15rem;
        color: inherit;
        i {
          display: -webkit-box;
          -webkit-box-align: center;
          -webkit-box-pack: center;
          margin-right: 11rem;
          width: 30rem;
          height: 30rem;
          color: #22d59c;
          img {
            transform: scale(1.5);
          }
        }
        & > span {
          color: rgba(26, 26, 26, 1);
          font-weight: 400;
        }
      }
    }
  }
}
.list {
  & > div {
    height: 50rem;
    width: 100vw;
    align-items: center;
    justify-content: center;
    // background-color: #22d59c;
    margin: 10rem 0;
    display: flex;
    color: gray;
    span {
      font-size: 16rem;
      font-weight: 500;
    }
    img {
      height: 50rem;
      width: 50rem;
    }
  }
  ul {
    li {
      position: relative;
      display: -webkit-box;
      height: 60rem;
      & > div {
        position: relative;
        -webkit-box-flex: 1;
        display: -webkit-box;
        -webkit-box-align: center;
        padding: 21rem;
        overflow: hidden;
        & > div {
          -webkit-box-flex: 1;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          & > h3 {
            display: -webkit-box;
            -webkit-box-align: center;
            overflow: hidden;
            font-size: 16rem;
            font-weight: 400;
            & > span {
              display: block;
              max-width: 100%;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
          }
          & > p {
            display: -webkit-box;
            -webkit-box-align: center;
            overflow: hidden;
            & > span {
              max-width: 94%;
              opacity: 0.5;
              display: block;
              font-weight: 500;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
          }
        }
      }
    }
  }
}
</style>
