<template>
  <div>
    <div class="back" @click="$router.back()">
      <img src="@/assets/back.svg" alt="" />
      排行榜
    </div>
    <div class="loading" v-if="loading_show">
      <img src="@/assets/loading.gif" alt="" />
    </div>
    <section v-else>
      <div class="playlist-msg">
        <div>
          <div class="playlist-pic">
            <img src="" alt="" />
            <img :src="play_list.coverImgUrl  + '?imageView=1&type=webp&thumbnail=270x0'" alt="" />
          </div>
          <div class="playlist-title">
            <h2>{{ play_list.name }}</h2>
            <div class="playlist-auth">
              <p>{{ play_list.name }} {{ createrDate }}</p>
              <p>更新时间：{{ updateDate }}</p>
            </div>
          </div>
        </div>
        <!-- 播放 -->
        <div @click="$emit('getList', play_list)">
          <img src="@/assets/play.svg" alt="" />
        </div>
      </div>
      <div class="list">
        <div class="listCount">
          <div class="box">
            排行榜
            <span>共{{ play_list.trackIds.length }}首</span>
          </div>
        </div>
        <ul>
          <li
            v-for="(item, index) in list"
            :key="item.id"
            @click="$emit('getSong', item)"
            :class="{ rankthree: index < 3 }"
          >
            <div>
              <span>{{ index + 1 }}</span>
            </div>
            <div>
              <h3>
                <span
                  >{{ item.name
                  }}{{
                    item.alia.length == 0 ? "" : "(" + item.alia.join("") + ")"
                  }}</span
                >
              </h3>
              <p>
                <span>
                  {{
                    item.ar.map((o) => o.name).join(" / ") +
                    " · " +
                    item.al.name
                  }}</span
                >
              </p>
            </div>
          </li>
        </ul>
        <div class="clickmore" @click="getList(offset)">
          <span v-show="!moreloading && list.length < play_list.trackIds.length"
            >点击加载更多歌曲</span
          >
          <span
            v-show="!moreloading && list.length >= play_list.trackIds.length"
            >到底了！！！</span
          >
          <img v-show="moreloading" src="@/assets/loading.gif" alt="" />
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      moreloading: false,
      loading_show: false,
      play_list: [],
      list: [],
      offset: 0,
    };
  },
  computed: {
    play_count: function () {
      let num = this.play_list?.playCount?.toString();
      if (num?.length <= 4) {
        return this.play_list.playCount;
      } else if (num?.length <= 8) {
        return (this.play_list.playCount / Math.pow(10, 4)).toFixed(1) + " 万";
      } else {
        return (this.play_list.playCount / Math.pow(10, 8)).toFixed(1) + " 亿";
      }
    },
    updateDate: function () {
      let d = new Date(this.play_list.updateTime);
      return d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate();
    },
    createrDate: function () {
      let d = this.play_list.updateTime - this.play_list.createTime;
      return "第" + Math.floor(d / (1000 * 60 * 60 * 24)) + "天";
    },
  },
  methods: {
    getList: function (offset) {
      this.moreloading = true;
      this.axios
        .get("https://apis.netstart.cn/music/playlist/track/all", {
          params: { id: this.$route.query.id, limit: 20, offset },
        })
        .then((res) => {
          this.moreloading = false;
          this.list.push(...res.data.songs);
          this.offset += 20;
        });
    },
  },
  created() {
    this.loading_show = true;
    Promise.all([
      this.axios.get("https://apis.netstart.cn/music/playlist/detail", {
        params: { id: this.$route.query.id },
      }),
      this.axios.get("https://apis.netstart.cn/music/playlist/track/all", {
        params: { id: this.$route.query.id, limit: 20, offset: 0 },
      }),
    ]).then(([d, a]) => {
      this.loading_show = false;
      this.play_list = d.data.playlist;
      this.list = a.data.songs;
      this.offset += 20;
    });
  },
};
</script>

<style lang="less" scoped>
.back {
  width: 100%;
  height: 30rem;
  background-color: #22d59c;
  position: fixed;
  z-index: 500;
  font-size: 20rem;
  text-align: center;
  color: white;
  font-weight: 500;
  img {
    position: absolute;
    left: 15rem;
    height: 30rem;
    width: 30rem;
  }
}
.loading {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.playlist-msg {
  position: relative;
  padding-top: 40rem;
  margin: 0 0 13rem;
  display: -webkit-box;
  -webkit-box-align: center;
  -webkit-box-orient: vertical;
  & > div:first-child {
    display: -webkit-box;
    -webkit-box-align: center;
    -webkit-box-orient: vertical;
    margin: 0 0 15rem;
    .playlist-pic {
      position: relative;
      display: block;
      margin-bottom: 25rem;
      width: 190rem;
      height: 190rem;
      border-radius: 18rem;
      box-shadow: 0 7rem 13rem rgb(0 0 0 / 21%);
      background-color: #dbdbdb;
      -webkit-user-select: none;
      & > img:first-child {
        position: absolute;
        top: 0;
        left: 0;
        height: 20rem;
        z-index: 4;
        border-top-left-radius: 18rem;
      }
      & > img:last-child {
        position: relative;
        z-index: 1;
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 18rem;
      }
    }
    .playlist-title {
      max-width: 84%;
      text-align: center;
      h2 {
        width: 100%;
        font-weight: 400;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        -webkit-user-select: none;
        margin-bottom: 12rem;
        font-size: 18rem;
      }
      .playlist-auth {
        width: 100%;
        font-weight: 400;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        -webkit-user-select: none;

        & > p:first-child {
          padding-left: 8rem;
          height: 23rem;
          display: -webkit-box;
          -webkit-box-pack: center;
          -webkit-box-align: center;
          width: 100%;
          font-weight: 400;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          -webkit-user-select: none;
          margin-bottom: 12rem;
          font-size: 16rem;
          color: #121212;
          span {
            margin-left: 5rem;
            font-size: 14rem;
            line-height: 24rem;
            display: -webkit-flex;
            max-width: 250rem;
          }
        }
        & > p:last-child {
          margin-top: 10rem;
          font-size: 14rem;
          color: grey;
        }
      }
    }
  }
  & > div:last-child {
    width: 190rem;
    height: 40rem;
    border-radius: 40rem;
    box-shadow: 0 6px 32px rgb(24 213 156 / 50%);
    background-color: #22d59c;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      height: 30rem;
      width: 30rem;
    }
  }
}
.list {
  & > div.listCount {
    display: -webkit-box;
    -webkit-box-align: center;
    -webkit-box-pack: justify;
    margin: 0 16rem -5rem;
    & > .box {
      font-size: 12rem;
      color: #808080;
      -webkit-user-select: none;
      & > span {
        margin-left: 5rem;
      }
    }
  }
  & > div.clickmore {
    height: 50rem;
    width: 100vw;
    align-items: center;
    justify-content: center;
    // background-color: #22d59c;
    margin: 0 0 10rem;
    display: flex;
    span {
      font-size: 16rem;
      font-weight: 500;
      color: grey;
    }
    img {
      height: 50rem;
      width: 50rem;
    }
  }
  ul {
    overflow: hidden;
    .rankthree {
      & > div:first-child {
        span {
          color: red;
        }
      }
    }
    & > li {
      display: -webkit-box;
      -webkit-box-align: center;
      padding: 10rem 7rem 10rem 0;
      & > div:first-child {
        display: -webkit-box;
        -webkit-box-align: center;
        -webkit-box-pack: center;
        -webkit-box-orient: vertical;
        margin-right: -16rem;
        width: 47rem;
        font-weight: 400;
        -webkit-user-select: none;
        & > span:first-child {
          display: block;
          font-size: 14rem;
        }
      }
      & > div:last-child {
        margin-left: 16rem;
        -webkit-box-flex: 1;
        & > h3 {
          display: -webkit-box;
          -webkit-box-align: center;
          -webkit-user-select: none;
          font-size: 100%;
          font-weight: 600;
          & > span {
            display: block;
            max-width: 100%;
            font-size: 16rem;
            color: #000;
            font-weight: 400;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
        }
        & > p {
          font-size: 12rem;
          color: rgba(0, 0, 0, 0.4);
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          -webkit-user-select: none;
        }
      }
    }
  }
}
</style>
