<template>
  <div class="playBar" @click="$emit('getSong')">
    <div
      class="song-pic"
      :class="{ running: playing }"
      :style="`background-image: url(${picsrc})`"
    >
      <img src="@/assets/card.png" alt="" />
    </div>
    <div class="song-msg">
      <h5>
        {{ song?.name }}<span> {{ songname }}</span>
      </h5>
      -
      <span>
        {{ songauth }}
      </span>
    </div>
    <span @click.stop="$emit('song-pause')" v-show="playing">
      <i class="pause"></i>
    </span>
    <span @click.stop="$emit('song-play')" v-show="!playing">
      <i class="play"></i
    ></span>
    <span class="menu" @click.stop="$emit('openPlayMenu')">
      <i></i>
    </span>
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    playing: Boolean,
  },
  computed: {
    picsrc: function () {
      if (this.song) {
        if (this.song.album) {
          return (
            this.song.album.blurPicUrl +
            "?imageView=1&type=webp&thumbnail=270x0"
          );
        } else {
          return this.song.al.picUrl;
        }
      } else {
        return "";
      }
    },
    songname: function () {
      if (this.song) {
        if (this.song.alias) {
          if (this.song.alias?.length == 0) {
            return "";
          } else {
            return "(" + this.song?.alias.join("") + ")";
          }
        } else {
          if (this.song.alia?.length == 0) {
            return "";
          } else {
            return "(" + this.song?.alia.join("") + ")";
          }
        }
      } else {
        return "";
      }
    },
    songauth: function () {
      if (this.song) {
        if (this.song.artists) {
          return this.song?.artists.map((o) => o.name).join(" / ");
        } else {
          return this.song?.ar.map((o) => o.name).join(" / ");
        }
      } else {
        return "";
      }
    },
  },
};
</script>

<style lang="less" scoped>
.playBar {
  position: fixed;
  z-index: 100;
  bottom: 0;
  background-color: #22d59c;
  box-shadow: 0 -3rem 25rem rgb(0 0 0 / 15%);
  width: 100%;
  height: 50rem;
  display: flex;
  align-items: center;
  justify-content: center;
  .song-pic {
    width: 30rem;
    height: 30rem;
    position: absolute;
    top: -10rem;
    left: 30rem;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    transform: rotate(0deg);
    animation: rotatePic 5s infinite linear;
    animation-play-state: paused;
    background-color: black;
    border-radius: 50%;
    & > img {
      transform: scale(1.8);
    }
  }

  .song-msg {
    color: #fff;
    display: flex;
    align-items: flex-end;
    h5 {
      width: 100rem;
      padding-right: 5rem;
      font-size: 14rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-break: normal;
      text-align: right;
    }
    & > span {
      width: 100rem;
      padding-left: 5rem;
      color: #e6e6e6;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-break: normal;
    }
  }

  .running {
    animation-play-state: running;
  }

  @keyframes rotatePic {
    form {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  & > span {
    position: absolute;
    right: 43rem;
    height: 30rem;
    width: 30rem;
    // background-color: black;
    i {
      position: absolute;
      background-size: cover;
      top: 0;
      width: 29rem;
      height: 29rem;
    }
    .pause {
      background-image: url(@/assets/pause.svg);
    }
    .play {
      background-image: url(@/assets/play.svg);
    }
  }

  .menu {
    right: 10rem;
    i {
      position: absolute;
      background-image: url(@/assets/menu.svg);
      width: 30rem;
      height: 26rem;
      background-size: cover;
    }
  }
}
</style>
