<template>
  <div class="playpage">
    <div
      class="playpage-mask"
      :style="`background-image: url(${picsrc})`"
    ></div>
    <div class="playpage-top">
      <div
        class="playpage-close"
        @click="$emit('playPageClose'), (discshow = true)"
      >
        <span>❯</span>
      </div>
      <div class="song-msg">
        <div class="song-name">
          <h3>
            {{ song?.name }}<span>{{ songname }}</span>
          </h3>
        </div>
        <div class="song-auth">
          <h5>{{ songauth }}</h5>
          <span>❯</span>
        </div>
      </div>
      <!-- <div class="playpage-share">
        <span></span>
      </div> -->
    </div>
    <section @click="discshow = !discshow">
      <PlayPageDisc
        :song="song"
        :playing="playing"
        v-show="discshow"
      ></PlayPageDisc>
      <PlayLyric
        :song="song"
        v-show="!discshow"
        :songLic="songLic"
        :currentTime="currentTime"
      ></PlayLyric>
    </section>
    <PlayPageControl
      :playing="playing"
      :duration="duration"
      :currentTime="currentTime"
      :song="song"
      @song-play="$emit('song-play')"
      @song-pause="$emit('song-pause')"
      @update-time="$emit('update-time', $event)"
      @moduleChange="$emit('moduleChange', $event)"
      :playmodle="playmodle"
      @openPlayMenu="$emit('openPlayMenu')"
      @songbefore="$emit('songbefore')"
      @songafter="$emit('songafter')"
    ></PlayPageControl>
  </div>
</template>

<script>
import PlayPageDisc from "./PlayPageDisc.vue";
import PlayPageControl from "./PlayPageControl.vue";
import PlayLyric from "./PlayLyric.vue";

export default {
  props: {
    currentTime: Number,
    duration: Number,
    song: Object,
    playing: Boolean,
    songLic: Object,
    playmodle: String,
  },
  computed: {
    picsrc: function () {
      if (this.song) {
        if (this.song.album) {
          return (
            this.song.album.blurPicUrl +
            "?imageView=1&type=webp&thumbnail=270x0"
          );
        } else {
          return this.song.al.picUrl + "?imageView=1&type=webp&thumbnail=270x0";
        }
      } else {
        return "";
      }
    },
    songname: function () {
      if (this.song) {
        if (this.song.alias) {
          if (this.song.alias?.length == 0) {
            return "";
          } else {
            return "(" + this.song?.alias.join("") + ")";
          }
        } else {
          if (this.song.alia?.length == 0) {
            return "";
          } else {
            return "(" + this.song?.alia.join("") + ")";
          }
        }
      } else {
        return "";
      }
    },
    songauth: function () {
      if (this.song) {
        if (this.song.artists) {
          return this.song?.artists.map((o) => o.name).join(" / ");
        } else {
          return this.song?.ar.map((o) => o.name).join(" / ");
        }
      } else {
        return "";
      }
    },
  },
  data: function () {
    return {
      discshow: true,
    };
  },
  components: { PlayPageDisc, PlayPageControl, PlayLyric },
};
</script>

<style lang="less" scoped>
.playpage {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  background-size: cover;
  background-color: #fff;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  & > section {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .playpage-mask {
    z-index: -1;
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    filter: blur(30rem);
    transform: scale(1.5);
    &::after {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.2);
    }
  }
  .playpage-top {
    display: flex;
    padding-top: 10rem;
    .playpage-close {
      display: flex;
      & > span {
        font-size: 22rem;
        color: #e6e6e6;
        padding: 10rem 27rem 10rem 27rem;
        transform: rotate(90deg);
        cursor: pointer;
        user-select: none;
      }
    }
    .song-msg {
      //   background-color: aqua;
      width: 250rem;
      overflow: hidden;
      white-space: nowrap;
      word-break: normal;
      text-align: center;
      position: relative;
      // flex: 1;

      .song-name {
        & > h3 {
          font-size: 22rem;
          color: #fff;
        }
      }
      .song-auth {
        color: #e6e6e6;
        display: flex;
        justify-content: center;

        h5 {
          color: #e6e6e6;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          word-break: normal;
        }

        span {
          padding-left: 10rem;
        }
      }
    }
    .playpage-share {
      display: flex;
      width: 18%;
      // background-color: red;
      position: relative;
      align-items: center;
      justify-content: center;
      & > span {
        background-position: -200px -400px;
        position: absolute;
        width: 32rem;
        height: 32rem;
        background-repeat: no-repeat;
        background-image: url(@/assets/icon.png);
      }
    }
  }
}
</style>
