<template>
  <div class="home">
    <section class="search">
      <div
        class="search-input"
        :class="{ inputing: inputing == true || isinput }"
      >
        <img src="@/assets/search.svg" alt="" />
        <div>
          <input
            type="text"
            placeholder="搜索"
            @focus="(inputing = true), (searchshow = true)"
            @blur="inputing = false"
            v-model="value"
            @keyup.esc="value = ''"
            @keyup.enter="
              $router.push({ path: '/searchview', query: { keywords: value } }),
                $emit('history', value)
            "
          />
        </div>
        <i v-show="value != ''" @click="value = ''"></i>
      </div>
      <span v-show="searchshow" @click="(searchshow = false), (value = '')"
        >取消</span
      >
    </section>

    <SearchPage
      v-show="searchshow"
      :hissearch="hissearch"
      class="SearchPage"
    ></SearchPage>

    <div class="loading" v-if="loading_show">
      <img src="@/assets/loading.gif" alt="" />
    </div>

    <section class="home-list" v-if="!loading_show" v-show="!searchshow">
      <h2>
        官方推荐
        <!-- <span>更多❯</span> -->
      </h2>
      <section>
        <ul class="gooderpalylist-list">
          <RecommendPlayList
            v-for="item in editors_choice"
            :item="item"
            :key="item?.id"
          ></RecommendPlayList>
        </ul>
      </section>
      <h2>
        达人歌单
        <!-- <span>更多❯</span> -->
      </h2>
      <section>
        <ul class="gooderpalylist-list">
          <RecommendPlayList
            v-for="item in good_choice"
            :item="item"
            :key="item?.id"
          ></RecommendPlayList>
        </ul>
      </section>
      <h2>最新音乐</h2>
      <ul class="new-song-list">
        <NewSong
          v-for="item in new_song"
          :item="item"
          :key="item?.id"
          @getSong="$emit('getSong', $event)"
          :playing="playing"
          @song-pause="$emit($refs.audio.pause())"
          @song-play="$emit('song-play')"
          :song="song"
        ></NewSong>
      </ul>
    </section>
  </div>
</template>

<script>
import NewSong from "@/components/NewSong.vue";
import RecommendPlayList from "@/components/RecommendPlayList.vue";
import SearchPage from "../components/SearchPage.vue";

export default {
  name: "HomeView",
  components: { NewSong, RecommendPlayList, SearchPage },
  props: {
    playing: Boolean,
    song: Object,
    hissearch: Array,
  },
  // watch:{
  //   hissearch: function (newhissearch) {
  //     localStorage.setItem("hissearch", JSON.stringify(newhissearch));
  //   },
  // },
  data: function () {
    return {
      editors_choice: [],
      new_song: [],
      good_choice: [],
      loading_show: false,
      inputing: false,
      searchshow: false,
      value: "",
    };
  },
  // methods: {
  //   history: function (keyword) {
  //     this.hissearch = [...new Set([keyword, ...this.hissearch])];
  //   },
  // },
  computed: {
    isinput: function () {
      return this.value != "";
    },
  },
  created() {
    Promise.all([
      this.axios.get("https://apis.netstart.cn/music/personalized", {
        params: { limit: 6 },
      }),
      this.axios.get(
        "https://apis.netstart.cn/music//top/playlist/highquality",
        {
          params: { limit: 6 },
        }
      ),
      this.axios.get("https://apis.netstart.cn/music/personalized/newsong"),
      (this.loading_show = true),
    ]).then(([g, e, n]) => {
      this.loading_show = false;
      this.editors_choice = e.data.playlists;
      this.new_song = n.data.result;
      this.good_choice = g.data.result;
    });
  },
  methods: {},
};
</script>
