<template>
  <li
    ref="box"
    class="playlist"
    @click="
      $router.push({ path: '/playlistview', query: { id: item.id } }),
        $refs.box.scrollTo(0, 0)
    "
  >
    <div class="playlist-pic">
      <img :src="item.picUrl || item.coverImgUrl | imgFilter" alt="" />
      <span>{{ play_count }}</span>
    </div>
    <div class="playlist-name">
      <h5>{{ item.name }}</h5>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
  },
  computed: {
    play_count: function () {
      let num = this.item.playCount?.toString();
      if (num?.length <= 4) {
        return this.item.playCount;
      } else if (num.length <= 8) {
        return (this.item.playCount / Math.pow(10, 4)).toFixed(1) + "万";
      } else {
        return (this.item.playCount / Math.pow(10, 8)).toFixed(1) + "亿";
      }
    },
  },
};
</script>

<style lang="less" scoped>
.playlist {
  display: inline-block;
  margin: 0 4rem;
  border-radius: 15rem;
  width: 109rem;
  position: relative;
  top: 0;
  .playlist-pic {
    width: 109rem;
    height: 109rem;
    border-radius: 8rem;
    overflow: hidden;
    position: relative;
    & > span {
      text-shadow: 1rem 0 0 rgb(0 0 0 / 15%);
      position: absolute;
      background: rgba(0, 0, 0, 0.4);
      border-radius: 9rem;
      right: 5rem;
      bottom: 2rem;
      font-size: 12rem;
      padding-right: 5rem;
      padding-left: 16rem;
      color: white;
      background-position: 3rem 4rem;
      background-size: 11rem 10rem;
      background-repeat: no-repeat;
      background-image: url(@/assets/playCount.png);
    }
  }
  .playlist-name {
    width: 109rem;
    margin-top: 8rem;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    white-space: normal;
    word-wrap: break-word;
    word-break: normal;
    color: rgba(26, 26, 26, 1);
    font-size: 13rem;
  }
}
</style>
