<template>
  <div class="playPageContorl">
    <div class="play-bar">
      <span>{{ playedtime }}</span>
      <PlayProgess
        :duration="duration"
        :tmpCurrentTime="tmpCurrentTime"
        @dragingtrue="dragingtrue"
        @update-time="$emit('update-time', $event)"
        @dragingfalse="dragingfalse"
      ></PlayProgess>
      <span>{{ songtime }}</span>
    </div>
    <div class="play-contorl">
      <span
        class="playsort-contorl"
        v-show="playmodle == 'playsort'"
        @click="$emit('moduleChange', 'playlistargin')"
      >
        <i></i>
      </span>
      <span
        class="playlistargin-contorl"
        v-show="playmodle == 'playlistargin'"
        @click="$emit('moduleChange', 'playargin')"
      >
        <i></i>
      </span>
      <span
        class="playargin-contorl"
        v-show="playmodle == 'playargin'"
        @click="$emit('moduleChange', 'playrandom')"
      >
        <i></i>
      </span>
      <span
        class="playrandom-contorl"
        v-show="playmodle == 'playrandom'"
        @click="$emit('moduleChange', 'playsort')"
      >
        <i></i>
      </span>
      <span @click.stop="$emit('songbefore')" class="before-contorl">
        <i></i>
      </span>
      <span @click.stop="$emit('song-pause')" v-show="playing">
        <i class="pause"></i>
      </span>
      <span @click.stop="$emit('song-play')" v-show="!playing">
        <i class="play"></i
      ></span>
      <span @click.stop="$emit('songafter')" class="after-contorl">
        <i></i>
      </span>
      <span class="menu-contorl" @click="$emit('openPlayMenu')">
        <i></i>
      </span>
    </div>
  </div>
</template>

<script>
import PlayProgess from "./PlayProgess.vue";
export default {
  props: {
    currentTime: Number,
    duration: Number,
    song: Object,
    playing: Boolean,
    playmodle: String,
  },
  data: function () {
    return {
      draging: false,
      tmpCurrentTime: 0,
    };
  },
  methods: {
    dragingtrue: function () {
      this.draging = true;
    },
    dragingfalse: function () {
      this.draging = false;
    },
  },
  computed: {
    playedtime: function () {
      return (
        (Math.floor(this.currentTime / 60) >= 10
          ? Math.floor(this.currentTime / 60)
          : "0" + Math.floor(this.currentTime / 60)) +
        ":" +
        (Math.floor(this.currentTime % 60) >= 10
          ? Math.floor(this.currentTime % 60)
          : "0" + Math.floor(this.currentTime % 60))
      );
    },
    songtime: function () {
      return (
        (Math.floor(this.duration / 60) >= 10
          ? Math.floor(this.duration / 60)
          : "0" + Math.floor(this.duration / 60)) +
        ":" +
        (Math.floor(this.duration % 60) >= 10
          ? Math.floor(this.duration % 60)
          : "0" + Math.floor(this.duration % 60))
      );
    },
  },
  watch: {
    currentTime: function (n) {
      if (!this.draging) {
        this.tmpCurrentTime = this.currentTime;
        this.tmpCurrentTime = n;
      }
    },
  },
  components: { PlayProgess },
};
</script>

<style lang="less" scoped>
.playPageContorl {
  height: 100rem;
