    background-color: white;
    position: absolute;
    left: 60rem;
    border-radius: 5rem;
    pointer-events: none;
  }
  .play-bar {
    color: white;
    display: flex;
    margin: 10rem;
    justify-content: space-between;
    align-items: center;
  }
  .play-contorl {
    position: absolute;
    width: 100%;
