    display: flex;
    & > span.playsort-contorl {
      height: 50rem;
      width: 30rem;
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        position: absolute;
        background-image: url(@/assets/player.png);
        height: 20rem;
        width: 23rem;
        background-position: 0 -260px;
      }
    }
    & > span.playrandom-contorl {
      height: 50rem;
      width: 30rem;
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        position: absolute;
        background-image: url(@/assets/player.png);
        height: 19rem;
        width: 25rem;
        background-position: 0 -74px;
      }
    }
    & > .playlistargin-contorl {
      height: 50rem;
      width: 30rem;
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        position: absolute;
        background-image: url(@/assets/player.png);
        height: 25rem;
        width: 26rem;
        background-position: 0 -205px;
      }
    }
    & > .playargin-contorl {
      height: 50rem;
      width: 30rem;
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        position: absolute;
        background-image: url(@/assets/player.png);
        height: 25rem;
        width: 26rem;
        background-position: 0 -232px;
      }
    }
    & > span.before-contorl {
      height: 50rem;
      width: 30rem;
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        position: absolute;
        background-image: url(@/assets/player.png);
        width: 19rem;
        height: 20rem;
        background-position: 0 -30px;
      }
    }
    & > span {
      //   position: absolute;
      height: 29rem;
      width: 21rem;
      margin: 0 10rem;
      // background-color: black;
      i {
        position: absolute;
        background-image: url(@/assets/player.png);
        width: 21rem;
        height: 29rem;
      }
      .pause {
        background-position: -30rem 0;
      }
      .play {
        background-position: 0 0;
      }
    }
    & > span.after-contorl {
