      display: flex;
      justify-content: center;
      align-items: center;
      i {
        position: absolute;
        background-image: url(@/assets/player.png);
        width: 19rem;
        height: 20rem;
        background-position: 0 -52px;
      }
    }
    & > span.menu-contorl {
      height: 50rem;
      width: 30rem;
      display: flex;
      justify-content: center;
