<template>
  <div>
    <div class="loading" v-if="loading_show">
      <img src="@/assets/loading.gif" alt="" />
    </div>
    <ul v-else>
      <RankListItem
        v-for="item in ranklist"
        :key="item.id"
        :item="item"
      ></RankListItem>
    </ul>
  </div>
</template>

<script>
import RankListItem from "@/components/RankListItem.vue";
export default {
  components: { RankListItem },
  data: function () {
    return {
      ranklist: [],
      loading_show: false,
    };
  },
  created() {
    (this.loading_show = true),
      this.axios
        .get("https://apis.netstart.cn/music/toplist/detail")
        .then((res) => {
          this.ranklist = res.data.list;
          this.loading_show = false;
        });
  },
};
</script>
<style lang="less" scoped>

.loading {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}
ul {
  margin-top: 116rem;
  padding-bottom: 55rem;
}
</style>
