<template>
  <div class="playPageDisc">
    <div class="disc">
      <div
        class="disc-pic"
        :class="{ running: playing }"
        :style="`background-image: url(${picsrc})`"
      >
        <img src="@/assets/disc.png" alt="" />
      </div>

      <div class="disc-zhen" :class="{ 'disc-zhen-put': playing }"></div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    playing: Boolean,
  },
  computed: {
    picsrc: function () {
      if (this.song) {
        if (this.song.album) {
          return (
            this.song.album.blurPicUrl +
            "?imageView=1&type=webp&thumbnail=270x0"
          );
        } else {
          return this.song.al.picUrl + "?imageView=1&type=webp&thumbnail=270x0";
        }
      } else {
        return "";
      }
    },
  },
};
</script>

<style lang="less" scoped>
.playPageDisc {
  height: 250rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  .disc {
    height: 200rem;
    width: 200rem;
    position: relative;
    .disc-pic {
      width: 100%;
      height: 100%;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      border-radius: 50%;
      transform: rotate(0deg);
      animation: rotatePic 5s infinite linear;
      animation-play-state: paused;
      background-color: black;
      position: relative;
      // background-color: red;
      & > img {
        width: 100%;
        height: 100%;
        transform: scale(1.3);
      }
    }

    @keyframes rotatePic {
      form {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }
    .running {
      animation-play-state: running;
    }
    .disc-zhen {
      &::after {
        content: "";
        width: 73rem;
        height: 118rem;
        top: -80rem;
        left: 90rem;
        transform: rotate(-30deg);
        background-image: url(@/assets/disc-zhen.png);
        background-size: cover;
        position: absolute;
        transform-origin: 10px 10px;
        transition: all 0.3s linear;
      }

      &.disc-zhen-put::after {
        transform: rotate(-5deg);
      }
    }
  }
}
</style>
