<template>
  <li
    @click="
      $router.push({ path: '/ranklistview', query: { id: item.id } })
    "
  >
    <div class="playlist-msg">
      <h2>{{ item.name }}</h2>
      <ol>
        <li v-for="(t, index) in songlist" :key="t.id">
          <strong>{{ index + 1 }}.</strong>
          <span>{{ t.al.name }}</span
          >-<span>{{ t.ar.map((o) => o.name).join(" / ") }}</span>
        </li>
      </ol>
    </div>
    <div class="playlist-pic">
      <img :src="item.coverImgUrl" alt="" />
      <span>{{ item.updateFrequency }}</span>
      <div>
        <span>{{ play_count }}</span>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
  },
  data() {
    return {
      songlist: [],
    };
  },
  created() {
    this.axios
      .get("https://apis.netstart.cn/music/playlist/track/all", {
        params: { id: this.item.id, limit: 3 },
      })
      .then((res) => {
        this.songlist = res.data.songs;
      });
  },
  computed: {
    play_count: function () {
      let num = this.item.playCount?.toString();
      if (num?.length <= 4) {
        return this.item.playCount;
      } else if (num.length <= 8) {
        return (this.item.playCount / Math.pow(10, 4)).toFixed(1) + " 万";
      } else {
        return (this.item.playCount / Math.pow(10, 8)).toFixed(1) + " 亿";
      }
    },

  },
};
</script>

<style lang="less" scoped>
li {
  display: -webkit-box;
  -webkit-box-align: center;
  margin: 0 16rem 12rem;
  border-radius: 8rem 0 0 8rem;
  .playlist-msg {
    -webkit-box-flex: 1;
    & > h2 {
      margin: 0 12rem 4rem 12rem;
      line-height: 22rem;
      font-size: 16rem;
      font-weight: 700;
    }
    ol {
      li {
        margin: 0 12rem 4rem;
        line-height: 16rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        list-style: none;
      }
    }
  }
  .playlist-pic {
    position: relative;
    width: 107rem;
    height: 107rem;
    overflow: hidden;
    border-radius: 0 8rem 8rem 0;
    & > span {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: 2;
      display: -webkit-box;
      -webkit-box-pack: end;
      height: 38rem;
      padding: 6rem;
      font-size: 10rem;
      color: #fff;
      background-image: -webkit-linear-gradient(
        top,
        rgba(0, 0, 0, 0.4),
        transparent
      );
    }
    & > div {
      position: absolute;
      bottom: 0;
      right: 0;
      z-index: 5;
      display: -webkit-box;
      -webkit-box-pack: center;
      -webkit-box-align: center;
      height: 17rem;
      min-width: 14rem;
      padding: 0 7rem;
      border-radius: 9rem;
      & > span {
        position: absolute;
        white-space: nowrap;
        background: rgba(0, 0, 0, 0.4);
        border-radius: 9rem;
        right: 5rem;
        bottom: 2rem;
        font-size: 12rem;
        padding-right: 5rem;
        padding-left: 16rem;
        color: white;
        background-position: 3rem 4rem;
        background-size: 11rem 10rem;
        background-repeat: no-repeat;
        background-image: url(@/assets/playCount.png);
      }
    }
  }
}
</style>
