<template>
  <div>
    <input
      type="range"
      min="0"
      :max="duration"
      :value="tmpCurrentTime"
      @input="$emit('dragingtrue')"
      @change="
        $emit('dragingfalse');
        $emit('update-time', $event.target.value);
      "
    />
  </div>
</template>

<script>
export default {
  props: {
    tmpCurrentTime: Number,
    duration: Number,
  },
};
</script>

