import Vue from 'vue'
import './plugins/axios'
import App from './App.vue'
import router from './router'

Vue.config.productionTip = false

Vue.filter("imgFilter", function (value,size = 270) {
      if (!value) return ''
      return `${value}?imageView=1&type=webp&thumbnail=${size}x0`
    })

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
