<template>
  <li class="new-song" @click="$emit('getSong', item.song)">
    <div class="new-song-msg">
      <div class="new-song-name">
        {{ item.name
        }}<span>{{
          item.song.alias.length == 0
            ? ""
            : "(" + item.song.alias.join("") + ")"
        }}</span>
      </div>
      <div class="new-song-auth">
        <i v-if="item.song.sqMusic != null">
          <img src="@/assets/sq.svg" alt="" />
        </i>
        {{
          item.song.artists.map((o) => o.name).join(" / ") +
          " - " +
          item.song.album.name
        }}
      </div>
    </div>
    <div v-if="song?.id !== item?.id" class="music-play">
      <span></span>
    </div>
    <div v-else class="music-play active" :class="{ playing: playing }">
      <span></span><span></span><span></span><span></span>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
    playing: Boolean,
    song: Object,
  },
};
</script>

<style lang="less" scoped>
.new-song {
  display: flex;
  padding-left: 10rem;
  position: relative;
  align-items: center;
  .new-song-msg {
    padding: 6rem 0;
    width: 0;
    flex: 1;
    .new-song-name {
      font-size: 17rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-break: normal;
    }
    .new-song-auth {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-break: normal;
      & > i {
        width: 12rem;
        height: 10rem;
        margin-right: 4rem;
        & > img {
          width: 12rem;
          height: 10rem;
        }
      }
    }
  }
  .music-play {
    padding: 0 10rem;
    display: flex;
    align-items: center;
    & > span {
      display: inline-block;
      width: 22rem;
      height: 22rem;
      background-position: -24rem 0;
      background-image: url(@/assets/play.png);
      background-size: 166rem 97rem;
      background-repeat: no-repeat;
    }
    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      height: 22rem;
      width: 44rem;
      span {
        width: 11%;
        height: 100%;
        background: #22d59c;
        animation: playingrunning 0.8s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }

      @keyframes playingrunning {
        from {
          transform: rotateX(15deg);
        }
        to {
          transform: rotateX(60deg);
        }
      }
      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
  &::after {
    position: absolute;
    z-index: 2;
    content: "";
    top: 0;
    left: 10rem;
    pointer-events: none;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    transform-origin: top left;
    border-bottom: 1rem solid rgba(0, 0, 0, 0.1);
  }
}
</style>
